
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  box-sizing: border-box;
  background: hsla(140, 23%, 61%, 0.787);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  background: url("./img/bg1.svg") ;
  /* color: white; */
}

#root{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
  width: 60vw;
  flex-direction: column;
}

.app{
  height: 100%;
  width: 100%;
  background-color: #fff;
  /* position: absolute;
  left: 50%;
  /* top: 50%; */
  /* transform: translateX(-50%); */ 
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: space-between; */
  border-radius: .5rem;
  
}

.dflex{
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
}



.dflex > h2{
  font-family: Monaco;
  font-size: 2rem;
  margin: 0;
}

.head{
  height: 15%;
  width: 100%;
  box-sizing: border-box;
  padding: .45rem;
  background: linear-gradient(180deg, rgba(77, 69, 148, 0.952), rgba(114, 200, 200, 0.952));
  color: whitesmoke;
  border-radius: inherit;
  /* border-style: solid 5px; */
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.head .c-name{
  font-size: 2.3rem;
  /* font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif; */
  margin-top: .5rem;
  /* opacity: .8 ; */
}

.head .tagline , .lead{
  color: hsla(273, 84%, 22%, 0.863);
  font-size: 1.1rem;
  font-style: italic;
  
}


.inp-data{
    height: 43%;
    width: 100%;
    /* padding: 1rem; */
    margin-top: .1rem;
}

.btn-red {
    display: block;
    height: 3rem;
    width: 5rem;
    cursor: pointer;
    background: rgba(219, 6, 70, 0.938);
    border: none;
    color: #f3f3f3;
    font-size: 1.2rem;
    border-radius: .5rem;
}

.inp-data > * {
    display: block;
    margin: auto;
    font-size: 1.2rem;
}

.inp-data > input{
  text-align: center;
}

.inp-data label{
  text-align: center;
  font-size: 1.3rem !important;
  margin: .5rem;
}

.field{
    width: 60%;
    height: 1.5rem;
    outline: none;
    border: #333 solid 1.5px;
    border-radius: .3rem;
    padding: .5rem;
    margin-top: .5rem;
    color: white;
    /* background: linear-gradient(
      #943939 0%,
      #a84f4f 45%,
      #531717 50%,
      #af7f7f 55%,
      #5e7694 100% ); */
     /* background:  linear-gradient(69deg, rgba(69, 119, 148, 0.952), rgba(64, 70, 128, 0.952)); */
     /* background:  linear-gradient(45deg, rgba(55, 134, 134, 0.856), rgba(31, 130, 134, 0.856)); 
     */
     background: hsla(226, 40%, 35%, 0.513);
}



.field:focus {
    border: #33d2ee 1.5px solid;
}

.field::placeholder{
  color: white;
}

.btn-black {
  margin-top: 1rem;
  display: block;
  /* height: 2rem; */
  width: 25%;
  padding: .7rem;
  cursor: pointer;
  background: #000000;
  color: white;
  border-radius: .5rem;
  outline: none;
  border: #fff 1px solid;
  transition: background .5s ease-in-out;
} 
.btn-black:hover{
  background: #39808d;
}

.tasks {
    height: 20rem;
    width: 100%;
    margin-top: 1rem;
   
    display: flex;
    flex-direction: column;
    overflow: auto;
    /* background-clip:unset; */
    background: rgba(192, 236, 235, 0.609);
    
    border-radius: .5rem;
    box-sizing: border-box;
    padding: 1rem;
    /* scroll-behavior: smooth; */
}

.task{
  display: flex;
  justify-content: space-between;
  padding: .5rem 1rem;
  /* background: rgba(82, 126, 115, 0.465); */
  background: #86BBBE;
  /* background: #4fb1b6; */
  margin: .3rem;
  border-radius: .5rem;
  transition: background .7s ease-in-out;
  /* word-wrap: break-word; */
  line-height: 1.5;
}

.task:hover{
  background: hsla(140, 23%, 61%, 0.787);
}

.work,
.time {
  display: block;
  padding: 2px;
  font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif
}

.time {
  font-weight: 600;
}

.work{
  font-size: 1.2em;
}

.task .btn{
/* background: #000000; */
color: #f3f3f3e3;
height: 3rem;
width: 3rem;
margin-left: 1rem;
outline: none;
/* border: #fff 2px solid; */ 
border: none;
position: relative; 
top: 2px; 
cursor: pointer;
border-radius: 50%;
transition: background .4s ease;
background: rgba(31, 130, 134, 0.856);
}
.btn:hover{
  /* background: #333; */
  background: rgba(202, 91, 81, 0.931);
  color: #000000;
}

.task .edit{
  background: rgba(58, 170, 114, 0.644);
  color: rgb(39, 38, 38);
}

.task .edit:hover{
  background: rgba(149, 228, 84, 0.945);
}

/* new min width */
.flex-task-btn{
  display: flex;
  justify-content: center;
  align-items: center;
  /* flex-wrap: wrap; */
;
}

